* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  color: #fff;
}

a {
  text-decoration: none;
  text-transform: uppercase;
}

a:hover {
  border-bottom: 3px solid #e63131;
  border-top: 3px solid #e63131;
}

.container-a,
.container-b,
.portfolio-header-container,
.portfolio-footer-container,
.preis-karte,
.portfolio-card,
.kontakt {
  -webkit-box-shadow: 7px 7px 7px -1px rgba(0, 0, 0, 0.43);
  box-shadow: 7px 7px 7px -1px rgba(0, 0, 0, 0.43);
}

/* ---------- Startseite ---------- */

.startseite-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;

  height: 100vh;
  background-color: #222831;
}

.logo-gro√ü {
  height: 550px;
}

.startseite-nav {
  display: flex;
  justify-content: space-between;
  width: 70%;
  margin: 0 auto;
}

.startseite-nav ul {
  display: flex;
  justify-content: space-between;
  width: 70%;
  margin: 0 auto;
}

.startseite-nav li {
  list-style: none;
}

.startseite-nav a {
  font-size: 1.4em;
}

/* ---------- Navigation ---------- */

.navigation {
  display: flex;
  justify-content: space-between;

  height: 15vh;
  background-color: #222831;
}

.navigation ul {
  width: 60%;
  display: flex;
  justify-content: space-between;
  margin: auto 0;
  margin-right: 100px;
}

.navigation li {
  list-style: none;
}

.logo-klein {
  width: 80px;
  margin-left: 30px;
  padding-bottom: 15px;
}

.navigation a {
  text-decoration: none;
  text-transform: uppercase;
  font-size: 1.3em;
}

/* ---------- Hintergrund ---------- */

.hintergrund-container {
  background-image: url("./images/background.gif");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 110% 110%;
  height: 85vh;
  position: relative;
}

.hintergrund-container h2 {
  text-transform: uppercase;
  font-size: 1.7em;
  text-align: center;
  padding-top: 30px;
}

/* ---------- Agentur ---------- */

.agentur-container {
  display: flex;
  justify-content: center;
  width: 100vw;
  padding-top: 50px;
}

.agentur-links {
  position: relative;
  height: 405px;
  width: 708px;
  background-color: #222831;
  margin-right: 40px;
}

.agentur-links h3 {
  padding: 40px 0px 0px 90px;
}

.agentur-rechts h3 {
  padding-top: 40px;
}

.agentur-links span,
.agentur-rechts span {
  color: #e63131;
  text-transform: uppercase;
}

.strich-links {
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 1;
}

.strich-rechts {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 1;
}

.person-container {
  display: flex;
  justify-content: space-around;
  margin-top: 25px;
}

#person-img {
  height: 380px;
  z-index: 10;
  margin-left: 10px;
}

.person-text,
.agentur-text {
  width: 60%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.agentur-text {
  padding-left: 60px;
  width: 80%;
}

.person-text h3 {
  padding: 15px 0px 30px 0px;
}

.agentur-text h3 {
  padding-bottom: 25px;
}

.person-text span,
.agentur-text span {
  letter-spacing: 5px;
}

.agentur-rechts {
  position: relative;
  height: 405px;
  width: 600px;
  background-color: #222831;
}

/* ---------- Portfolio ---------- */

.portfolio-header-container {
  height: 75px;
  background-color: #222831;
  margin: 30px auto 0 auto;
  width: 90%;
}

.portfolio-footer-container {
  position: relative;
  height: 400px;
  background-color: #222831;
  margin: 30px auto 0 auto;
  width: 90%;
}

.portfolio-beschreibung {
  text-align: center;
  padding: 15px 10px;
}

.strich-rechts-portfolio {
  position: absolute;
  right: 0;
  bottom: 0;
}

.strich-links-portfolio {
  position: absolute;
  left: 0;
  top: 0;
  transform: rotate(90deg);
}

.portfolio-card-container {
  z-index: 10;
}

.portfolio-footer-container {
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
}

.portfolio-card {
  height: 270px;
  width: 220px;
  background-color: #2e383f;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.portfolio-card h3 {
  margin: 20px 0;
}

.portfolio-card img {
  width: 150px;
  height: auto;
  margin: 15px 0;
}

.portfolio-card p {
  margin-bottom: 10px;
}

.button {
  padding: 5px 10px;
}

.button span {
  font-weight: 600;
  cursor: pointer;
}

/* ---------- Preise ---------- */

.preis-container {
  display: flex;
  justify-content: space-around;
  margin-top: 60px;
}

.preis-container h2 {
  margin: 0 30px;
  padding-bottom: 15px;
  border-bottom: 3px solid #e63131;
}

.preis-container span {
  color: #e63131;
}

.preis-karte {
  height: 400px;
  width: 340px;
  background-color: #222831;
}

.preis-info {
  display: flex;
  flex-direction: column;
}

.preis-text {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 40px;
  height: 280px;
}

.preis-text p {
  font-size: 1.2em;
  margin-bottom: 20px;
  text-align: center;
}

.preis {
  text-align: center;
  font-size: 1.5em;
}

/* ---------- Kontakt ---------- */

.kontakt-container {
  margin-top: 60px;
  display: flex;
  justify-content: center;
}

.kontakt {
  width: 900px;
  height: 460px;
  background-color: #222831;
  display: flex;
}

.karte-container,
.kontakt-info-container {
  padding: 70px 0px 0px 40px;
}

.kontakt img {
  width: 550px;
  height: auto;
}

.kontakt-heading {
  font-size: 1.1em;
  margin-bottom: 15px;
}

.kontakt-heading span {
  color: #e63131;
  text-transform: uppercase;
  letter-spacing: 5px;
}

.kontakt-info-top {
  margin-bottom: 20px;
}

.kontakt-info-bottom {
  margin-top: 20px;
}

.kontakt-info-top,
.kontakt-info-bottom {
  padding: 5px 0px;
}

/* ---------- Vorschau Bestway ---------- */

.vorschau-container {
  background-color: #222831;
  padding-top: 30px;
}

.background-second {
  background-image: url("./images/background-second.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 110% 110%;
  height: 87vh;
}

.vorschau-nav {
  height: 70px;
}

.vorschau-nav ul {
  display: flex;
  justify-content: space-around;
}

.vorschau-nav li {
  list-style: none;
  font-size: 1.3em;
}

.vorschau-karte {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.vorschau-karte h2 {
  margin-top: 45px;
  margin-bottom: 60px;
}

.vorschau-karte img {
  width: 800px;
  height: auto;
}

/* ---------- Footer ---------- */

.footer {
  position: absolute;
  width: 100%;
  bottom: 0;
  display: flex;
  justify-content: center;
}

.footer p {
  padding: 10px;
}
